.planet {
  display: grid;

  grid-template-columns: repeat(12, 6.5rem);
  gap: 3rem;
  grid-template-rows: repeat(5, min-content);
  justify-content: center;
  align-items: center;
  margin: 10rem;
  margin-bottom: 10rem;
  margin-top: 8rem;
  align-items: center;
  color: #ffffff;
}

.heading {
  font-size: 8rem;
  font-family: "Antonio", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  grid-column: 9/12;
  grid-row: 1/2;
}

.image {
  grid-column: 1/9;
  grid-row: 1/5;
  justify-self: center;
}

.content {
  font-family: "Spartan", sans-serif;
  font-weight: 500;
  font-size: 1.4rem;
  columns: #ffffff;
  grid-row: 2/3;
  grid-column: 9/-1;
  line-height: 2;
  /* line-height: 2; */
}

.source {
  grid-row: 3/4;
  grid-column: 9/13;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  gap: 1rem;
  font-family: "Spartan", sans-serif;
  font-size: 1.4rem;
}

.sourceName {
}

.sourceLink {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #838391;
  cursor: pointer;
}

.list {
  font-family: "Spartan", sans-serif;
  font-size: 1.2rem;
  grid-row: 4/5;
  grid-column: 9/-1;
}

.lists {
  list-style: none;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  gap: 3rem;
}

.item {
  display: flex;
  border: 1px solid #838391;
  gap: 2rem;
  cursor: pointer;
  padding: 1.5rem 2rem;
  text-transform: uppercase;
  letter-spacing: 2.57px;
}

.number {
  opacity: 0.5;
  padding-right: 1rem;
}

.name {
  font-weight: 700;
  text-decoration: none;
  color: #ffffff;
}

.active {
  background-color: #419ebb;
}
