.header {
  display: grid;
  grid-template-columns: 1fr max-content;

  justify-content: center;
  align-items: center;
  background-color: black;
  padding: 2.2rem 3.2rem;
  /* padding-top: 2.2rem; */
  /* margin: 3.2rem 2; */
  /* padding: 2rem; */
}

.logo {
  font-family: "Antonio", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  color: #ffffff;
  letter-spacing: -1.05px;
  /* margin-left: 3.2rem; */
  font-size: 2.8rem;
}

.navBar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3.3rem;
  /* padding-right: 3.3rem; */
}

.navLink {
  text-decoration: none;
}

.navName {
  font-family: "Spartan", sans-serif;
  font-weight: 700;
  font-size: 1.1rem;
  text-transform: uppercase;
  color: #ffffff;
  letter-spacing: 1px;
}
